body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.navbar{
  height:60px;
  width: 100%;
  background-color: aqua;
}
.login-signup-container{
  width: 400px;
  border-radius: 20px;
  border: 1px Solid gainsboro;
  margin: auto;
  margin-top: 50px;
}
#login{
  background-color: chocolate;  
}
.login-signup-form{
  width:80%;
  margin:auto;
  margin-top:50px;
}
.input-form{
  height:40px;
  width:100%;
  border-radius:20px;
  margin:10px 0px;
  outline:none;
}
.btn{
  margin: 25px 0px 50px 0px;
  background-color: cornflowerblue;
  height: 50px;
  width: 100%;
  border-radius: 20px;
  cursor: pointer;
  outline: none;
}
.admin-log-in-form{
  background-color: chocolate;
  height: 35px;
  text-align: center;
  width:90%;
  margin: auto;
  margin-top: 20px;
  border-radius: 20px;
  padding-top: 10px;
}
.profile-container{
  width:80%;
  margin: 100px auto;

  border: 1px solid gainsboro;
  display: flex;
  flex-direction: column;
}
.user-images{
  display: flex;
  justify-content: space-around;
  margin-top: 50px;
}
.user-images div{
  
}
.user-details{
  font-weight: bolder;
  margin: 50px;
  font-family: 'Courier New', Courier, monospace;
}
.user-img{
  height: 180px;
  width: 180px;
  border: 1px solid gainsboro;
}
.add-image-button{
  background-color:grey;
  text-align: center;
  cursor: pointer;
  padding:10px;
}
.camera-container{
  margin-top: -800px;
}